.home-page {
  /* padding-top: 159px; */
}

.intro {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-bottom: 8.9rem;
}

.heading {
  font-size: 3.9rem;
  font-weight: 400;
}

.intro {
  padding: 4.7rem 10.5rem;
}

.intro p, .para {
  font-size: 2.5rem;
}

.green-hr {
  width: 68.8rem;
  border: 2px solid #20ce88;
  border-radius: 4px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  margin-top: 1.3rem;
}

.button {
  font-size: 2.2rem;
  padding: 1.8rem 5rem;
  background-color: #00296b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.cards {
  padding: 0 14.5rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 8.5rem;
}

.card {
  width: 35rem;
  text-align: center;
}

.green-text {
  color: #20ce88;
  font-weight: 700;
  font-size: 3.9rem;
}

.products {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 0 14.5rem;
  margin-bottom: 11.2rem;
}

.product-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 5.7rem;
}

.product-card {
  width: 37.3rem;
  height: 60rem;
  border: 2px solid #00296b;
  border-radius: 5px;
  position: relative;
}
