@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.header {
  padding: 4.3rem 6.1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;  
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1;
  background: white;
}

.nav {
  display: flex;
  align-items: center;
  margin-left: 3.6rem;
  margin-right: auto;
}

.header-link {
  text-decoration: none;
  color: #00296B;
  font-size: 2.5rem;
  margin: 0 1.3rem;
  white-space: nowrap;
}

.search, .globe-icon  {
  display: flex;
  align-items: center;
}

.globe-icon {
  margin-right: 2.1rem;
}

.search input{
  font-size: 16px;
}

.footer {
  padding: 6rem 11.2rem;
  background: #121330;
  width: 100%;
  color: #fff;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
}

.footer p {
font-size: 2rem;
}

.footer-grid h1 {
    font-size: 2.5rem;
}

/* .footer-grid div {
    padding: 50px;
    font-size: 20px;
    background-color: brown;
    border: 1px solid white;
} */
.home-page {
  /* padding-top: 159px; */
}

.intro {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-bottom: 8.9rem;
}

.heading {
  font-size: 3.9rem;
  font-weight: 400;
}

.intro {
  padding: 4.7rem 10.5rem;
}

.intro p, .para {
  font-size: 2.5rem;
}

.green-hr {
  width: 68.8rem;
  border: 2px solid #20ce88;
  border-radius: 4px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  margin-top: 1.3rem;
}

.button {
  font-size: 2.2rem;
  padding: 1.8rem 5rem;
  background-color: #00296b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.cards {
  padding: 0 14.5rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 8.5rem;
}

.card {
  width: 35rem;
  text-align: center;
}

.green-text {
  color: #20ce88;
  font-weight: 700;
  font-size: 3.9rem;
}

.products {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 0 14.5rem;
  margin-bottom: 11.2rem;
}

.product-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 5.7rem;
}

.product-card {
  width: 37.3rem;
  height: 60rem;
  border: 2px solid #00296b;
  border-radius: 5px;
  position: relative;
}

.products-page {
    display: flex;
    flex-flow: column;
    align-items: center;
    padding: 0 14.7rem;
}

.product-container {
    margin-bottom: 11.2rem;
}
.about-us-page {
  padding: 0 14.7rem;
}

.flex {
  display: flex;
  justify-content: center;
}
.title-des-box {
  position: relative;
}

.box-title-left,
.box-title-right {
  width: 49.8rem;
  background-color: #00296b;
  text-align: center;
  color: white;
  padding: 3rem 0;
  font-size: 3.9rem;
  border-radius: 5px;
}

.box-description-left,
.box-description-right {
  border: 2px solid #20ce88;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-size: 2.6rem;
  padding: 7.5rem 1.2rem;
  width: 70%;
}

.box-description-right {
  transform: translateX(30%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-family: roboto;
}

.App {
  position: relative;
  padding-top: 15.9rem;
}
