.footer {
  padding: 6rem 11.2rem;
  background: #121330;
  width: 100%;
  color: #fff;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
}

.footer p {
font-size: 2rem;
}

.footer-grid h1 {
    font-size: 2.5rem;
}

