.flex {
  display: flex;
  justify-content: center;
}
.title-des-box {
  position: relative;
}

.box-title-left,
.box-title-right {
  width: 49.8rem;
  background-color: #00296b;
  text-align: center;
  color: white;
  padding: 3rem 0;
  font-size: 3.9rem;
  border-radius: 5px;
}

.box-description-left,
.box-description-right {
  border: 2px solid #20ce88;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-size: 2.6rem;
  padding: 7.5rem 1.2rem;
  width: 70%;
}

.box-description-right {
  transform: translateX(30%);
}
