.header {
  padding: 4.3rem 6.1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;  
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1;
  background: white;
}

.nav {
  display: flex;
  align-items: center;
  margin-left: 3.6rem;
  margin-right: auto;
}

.header-link {
  text-decoration: none;
  color: #00296B;
  font-size: 2.5rem;
  margin: 0 1.3rem;
  white-space: nowrap;
}

.search, .globe-icon  {
  display: flex;
  align-items: center;
}

.globe-icon {
  margin-right: 2.1rem;
}

.search input{
  font-size: 16px;
}
